@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Medium.ttf") format("truetype"), url("../fonts/Manrope-Medium.woff") format("woff"), url("../fonts/Manrope-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Manrope";
  src: url("../fonts/Manrope-Bold.ttf") format("truetype"), url("../fonts/Manrope-Bold.woff") format("woff"), url("../fonts/Manrope-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("truetype"), url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "OrchideaPro";
  src: url("../fonts/OrchideaPro-Medium.ttf") format("truetype"), url("../fonts/OrchideaPro-Medium.woff") format("woff"), url("../fonts/OrchideaPro-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "OrchideaPro";
  src: url("../fonts/OrchideaPro-MediumItalic.ttf") format("truetype"), url("../fonts/OrchideaPro-Mediumtalic.woff") format("woff"), url("../fonts/OrchideaPro-MediumItalic.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: italic;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.sectionInner {
  padding: 20px;
  height: 100%;
  width: 100%;
}

a,
a:active,
a:visited {
  color: #FFFFFF;
  text-decoration: none;
}

.sectionHeader {
  background: linear-gradient(0deg, #464341 0%, #010101 100%);
  margin: 0 0 30px 0;
}
.sectionHeader .sectionInner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 1.5rem;
  font-style: normal;
}
.sectionHeader_logo {
  width: 120px;
  height: 20px;
}
.sectionHeader_phone {
  display: none;
}
.sectionHeader_phone-mobView {
  display: block;
}

.tabs {
  font-size: 0;
  /* отобразить контент, связанный с вабранной радиокнопкой (input type='radio') */
}
.tabs input[type=radio] {
  display: none;
}
.tabsContent {
  /* скрыть контент по умолчанию */
  display: none;
  font-size: 16px;
}
.tabs #tab-btn-1:checked ~ #content-1,
.tabs #tab-btn-2:checked ~ #content-2 {
  display: block;
}
.tabsLabel {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  user-select: none;
  color: #141414;
  border: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
  cursor: pointer;
  position: relative;
  top: 1px;
  margin: 0 20px 60px 0;
  text-transform: uppercase;
  font-family: "Manrope", sans-serif;
  font-weight: 700;
  font-size: 1.125rem;
  font-style: normal;
  line-height: 1.2;
}
.tabsLabel:first-of-type {
  margin-left: 20px;
}
.tabsLabel:last-of-type {
  margin-right: 20px;
}
.tabs input[type=radio]:checked + label {
  border-bottom: 3px solid #141414;
}

.tab_title {
  font-family: "OrchideaPro", sans-serif;
  font-weight: 500;
  font-size: 2.25rem;
  font-style: normal;
  line-height: 1.2;
  color: #141414;
  text-transform: uppercase;
  margin: 0 0 30px 0;
  padding: 0 20px;
}
.tab_title i {
  text-transform: none;
}

.firstTab_title {
  font-family: "OrchideaPro", sans-serif;
  font-weight: 500;
  font-size: 2.25rem;
  font-style: normal;
  line-height: 1.2;
  color: #141414;
  text-transform: uppercase;
  margin: 0 0 30px 0;
  padding: 0 20px;
}
.firstTab_title i {
  text-transform: none;
}
.firstTab_cards-item {
  margin: 0 0 60px 0;
}
.firstTab_cards-item-textContent {
  margin: 0 0 60px 0;
  padding: 0 20px;
}
.firstTab_cards-item-title {
  font-family: "OrchideaPro", sans-serif;
  font-weight: 500;
  font-size: 1.5rem;
  font-style: normal;
  line-height: 1.2;
  color: #141414;
  margin: 0 0 20px 0;
}
.firstTab_cards-item-paragraph {
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 0.875rem;
  font-style: normal;
  line-height: 1.3;
  color: #141414;
  margin: 0 0 10px 0;
}
.firstTab_cards-item-paragraph:last-child {
  margin: 0;
}
.firstTab_cards-item-img img {
  width: 100%;
  height: 370px;
  object-fit: cover;
  object-position: 35% 50%;
}

.secondTab {
  position: relative;
  margin: 0 0 80px 0;
}
.secondTab .swiper {
  width: 100%;
  height: 100%;
}
.secondTab .swiper::after, .secondTab .swiper::before {
  z-index: 999;
  content: "";
  display: block;
  position: absolute;
  right: -3px;
  top: 0;
  width: 54px;
  height: 100%;
  background: linear-gradient(270deg, #FFFFFF 0%, rgba(255, 255, 255, 0.05) 100%);
}
.secondTab .swiper::before {
  right: auto;
  left: -3px;
  background: linear-gradient(90deg, #FFFFFF 0%, rgba(255, 255, 255, 0.05) 100%);
}
.secondTab .swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.secondTab .swiper-slide img {
  display: block;
  width: 100%;
  height: 400px;
  object-fit: none;
  max-width: 230px;
}
.secondTab .swiper-slide .text {
  background: rgba(20, 20, 20, 0.8);
  padding: 35px 30px;
  width: 100%;
  text-align: left;
  color: #FFFFFF;
  font-family: "OrchideaPro", sans-serif;
  font-weight: 500;
  font-size: 1.5rem;
  font-style: normal;
  line-height: 1.1;
  max-width: 230px;
}
.secondTab .swiper-slide .right {
  position: absolute;
  right: 0;
  width: 50% !important;
  height: 100%;
}
.secondTab .swiper-slide .bottom {
  position: absolute;
  bottom: 0;
}
.secondTab .swiper-button-next, .secondTab .swiper-button-prev {
  display: none;
  bottom: -50px;
  top: auto;
  right: 40%;
  transform: translate(50%, 0);
}
.secondTab .swiper-button-prev {
  right: auto;
  left: 40%;
  transform: translate(-50%, 0);
}
.secondTab .swiper-button-next:after, .secondTab .swiper-button-prev:after {
  content: url(../img/svg/next.svg);
  display: block;
  width: 48px;
  height: 48px;
}
.secondTab .swiper-button-prev:after {
  content: url(../img/svg/prev.svg);
}

.fancybox__slide::before, .fancybox__slide::after {
  content: none;
}

.fancybox__content {
  height: 80% !important;
}

.fancybox__caption {
  height: 20% !important;
  font-family: "Manrope", sans-serif;
  font-weight: 500;
  font-size: 1.125rem;
  font-style: normal;
  line-height: 1.3;
}
.fancybox__caption::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 16%;
  background: linear-gradient(180deg, rgba(20, 20, 20, 0) 0%, #141414 76.62%);
}

.fancybox__slide {
  padding-bottom: 66px;
  height: 100%;
}

.fancybox__nav .carousel__button.is-next,
.fancybox__nav .carousel__button.is-prev {
  top: auto;
  bottom: 19%;
  transform: translate(0, -100%);
  background-color: rgba(20, 20, 20, 0.3);
  border-radius: 0;
}

.fancybox__nav .carousel__button.is-prev {
  right: 56px;
  left: auto;
}

body:not(.is-using-mouse) .fancybox__container :focus {
  box-shadow: none;
}

.sectionFooter {
  height: 200px;
  background-color: #141414;
}
.sectionFooter .sectionInner {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.sectionFooter .sectionFooter_coopyright {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  font-style: normal;
  color: #FFFFFF;
}